import axios from 'axios';

const API_HOST = 'https://yihsuan-chen-s.azurewebsites.net/api/wedding';

const postForm = async function (name, phone, releation, attend, attendCount, childChair, vegeType, invitation, invitationEmail, said) {
  return new Promise(resolve => {
    axios({
      method: 'POST',
      url: API_HOST,
      headers: {
        'Content-Type': 'application/json'
      },
      data: {
        name: name,
        phone: phone,
        releation: releation,
        attend: attend,
        attend_count: attendCount,
        child_chair: childChair,
        vege_type: vegeType,
        invitation: invitation,
        invitation_email: invitationEmail,
        said: said
      }
    }).catch(function (error) {
      console.log(error);
    }).then(function (response) {
      resolve(response.status !== 200 ? null : response.data);
    });
  });
};

export default {
  postForm
};
