<template>
  <div>
    <div :class="`${isMobile() ? '' : 'font-20 '}mb-1${align ? ` text-${align}` : ''}`" v-for="line in text.split('\n')" :key="line.id">
      <span v-if="line">{{ line }}</span>
      <br v-else>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TextContent',

  props: {
    text: {
      type: String,
      required: true
    },
    align: {
      type: String
    }
  },

  methods: {
    isMobile () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        case 'md':
          return false;
        case 'lg':
          return false;
        case 'xl':
          return false;
      }
    }
  }
};
</script>
